              />
            </div>
            {#if i < 9}
              <figcaption class="lanking_title">{top.title}</figcaption>
            {:else}
              <figcaption class="lanking_title2">{top.title}</figcaption>
            {/if}
          </figure>
          <div class="hover_box">
