import axios from "axios";
import { writable } from "svelte/store";
//1. 
const setPromise = (url) => {
  const options = {
    method: "GET",
    url: url,
    params: { language: "ko", page: "1" },
    headers: {
      accept: "application/json",
      Authorization:
        "Bearer eyJhbGciOiJIUzI1NiJ9.eyJhdWQiOiI5NmRiMWFmY2ZkZTg3NTY3OTJlMzZmYjA1ZjBmNjc2MyIsIm5iZiI6MTcyODM1NTA5My42MzQ5NDQsInN1YiI6IjY3MDM5OWE2YjZmNTg3ZWRlODEzOGMyNSIsInNjb3BlcyI6WyJhcGlfcmVhZCJdLCJ2ZXJzaW9uIjoxfQ.wqmJkSiX1Vp4tEAj_on2LHdGlr9eP_RTbo9ippX-1Ow",
    },
  };
  const getPromise = async () => {
    try {
      const res = await axios.request(options);
      return res;
    } catch (error) {
      throw new Error(error);
    }
  };
  const { subscribe } = writable(getPromise());
  return {
    subscribe,
  };
};
