<script>
  import { links } from "svelte-routing";
  import Icon from "@iconify/svelte";
  import { onMount } from "svelte"; //onMount  
  //header   
  let header;
  //  
  const handleScroll = () => {
    let windowTop = window.scrollY;
    if (windowTop > 50) {
      header.classList.add("active");
    } else {
      header.classList.remove("active");
    }
  };
  //    
  onMount(() => {
    const timer = setInterval(() => {
      window.addEventListener("scroll", handleScroll);
    }, 100);
    return () => {
      clearInterval(timer);
      window.removeEventListener("scroll", handleScroll);
    };
  });
  //current :   a    
  let current = "";
</script>

<header use:links bind:this={header}>
  <!-- a  class:active on:click -->
  <h1>
    <a href="/" class:active={current === ""} on:click={() => (current = "")}
      ><img src="./img/moving_logo.svg" alt="" /></a
    >
  </h1>
  <ul>
    <!-- a  class:active on:click -->
    <li>
      <a
        href="/now"
        class:active={current === "now"}
        on:click={() => (current = "now")}
        ><Icon icon="ic:round-live-tv" color="white" height="28" /></a
      >
    </li>
    <li>
      <a
        href="/popular"
        class:active={current === "popular"}
        on:click={() => (current = "popular")}></a
      >
    </li>
    <li>
      <a
        href="/upcoming"
        class:active={current === "upcoming"}
        on:click={() => (current = "upcoming")}></a
      >
    </li>
    <li>
      <a
        href="/top"
        class:active={current === "top"}
        on:click={() => (current = "top")}></a
      >
    </li>
  </ul>
</header>
